import React from 'react';
import { useNavigate } from 'react-router-dom';
import goBackIcon from '../icons/goback.webp';
import "./PayBill.css"

const PayBill = () => {
    const navigate = useNavigate(); // Initialize useNavigate for redirection

    return (
        <div className="PayBill">
            <button className="go-back-button" onClick={() => navigate(-1)}>
                <img src={goBackIcon} alt="Go Back" className="go-back-button-img" />
            </button>
            <h2>Betal regninger</h2>
            <form>
                <div>
                    <label htmlFor="billAmount">Regningsbelb:</label>
                    <input type="number" id="billAmount" placeholder="Indtast belb" required />
                </div>
                <div>
                    <label htmlFor="billCompany">Virksomhed:</label>
                    <input type="text" id="billCompany" placeholder="Indtast virksomhed" required />
                </div>
                <button type="submit">Betal</button>
            </form>
        </div>
    );
};

export default PayBill;
