import React from 'react';
import { BrowserRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
import { isMobile } from './utils/isMobile';
import { AuthProvider, useAuth } from './AuthContext';
import Welcome from './pages/Welcome';
import Login from './pages/Login';
import DashboardLayout from './pages/DashboardLayout';
import Home from './pages/Home';
import Security from './pages/Security';
import Add from './pages/Add';
import Profile from './pages/Profile';
import Settings from './pages/Settings';
import Questions from './pages/Questions';
import NotMobileMessage from './components/NotMobileMessage';
import FaceIDSettings from './pages/FaceIDSettings'; 
import PasswordSettings from './pages/PasswordSettings'; 
import AccountInfo from './pages/AccountInfo'; 
import PrivacySettings from './pages/PrivacySettings'; 
import TwoFactorAuth from './pages/TwoFactorAuth';
import Notifications from './pages/Notifications'; 
import AccountDetails from './pages/AccountDetails'; 
import TransferMoney from './pages/TransferMoney'; // Import TransferMoney page
import PayBill from './pages/PayBill'; // Import PayBill page
import DepositCheck from './pages/DepositCheck'; // Import DepositCheck page
import ViewStatements from './pages/ViewStatements'; // Import ViewStatements page

const AppRoutes = () => {
    const { isAuthenticated } = useAuth();
    const mobileCheck = isMobile();

    return (
        <Routes>
            <Route path="/" element={mobileCheck ? <Welcome /> : <NotMobileMessage />} />
            <Route path="/login" element={mobileCheck ? <Login /> : <NotMobileMessage />} />
            <Route path="/questions" element={mobileCheck ? <Questions /> : <NotMobileMessage />} />

            {/* Protected Routes */}
            <Route path="/dashboard" element={mobileCheck ? (isAuthenticated ? <DashboardLayout /> : <Navigate to="/login" replace />) : <NotMobileMessage />}>

                <Route index element={isAuthenticated ? <Home /> : <Navigate to="/login" replace />} />
                <Route path="home" element={isAuthenticated ? <Home /> : <Navigate to="/login" replace />} />
                <Route path="profile" element={isAuthenticated ? <Profile /> : <Navigate to="/login" replace />} />
                <Route path="security" element={isAuthenticated ? <Security /> : <Navigate to="/login" replace />} />
                <Route path="settings" element={isAuthenticated ? <Settings /> : <Navigate to="/login" replace />} />
                <Route path="settings/face-id" element={isAuthenticated ? <FaceIDSettings /> : <Navigate to="/login" replace />} />
                <Route path="settings/password" element={isAuthenticated ? <PasswordSettings /> : <Navigate to="/login" replace />} />
                <Route path="settings/account-info" element={isAuthenticated ? <AccountInfo /> : <Navigate to="/login" replace />} />
                <Route path="settings/privacy-settings" element={isAuthenticated ? <PrivacySettings /> : <Navigate to="/login" replace />} />
                <Route path="settings/two-factor-auth" element={isAuthenticated ? <TwoFactorAuth /> : <Navigate to="/login" replace />} />
                <Route path="notifications" element={isAuthenticated ? <Notifications /> : <Navigate to="/login" replace />} />
                <Route path="account/:accountNumber" element={isAuthenticated ? <AccountDetails /> : <Navigate to="/login" replace />} />
                <Route path="add" element={isAuthenticated ? <Add /> : <Navigate to="/login" replace />} />

                {/* New Routes for Actions */}
                <Route path="transfer-money" element={isAuthenticated ? <TransferMoney /> : <Navigate to="/login" replace />} />
                <Route path="pay-bill" element={isAuthenticated ? <PayBill /> : <Navigate to="/login" replace />} />
                <Route path="deposit-check" element={isAuthenticated ? <DepositCheck /> : <Navigate to="/login" replace />} />
                <Route path="view-statements" element={isAuthenticated ? <ViewStatements /> : <Navigate to="/login" replace />} />
            </Route>

            {/* Catch-all for unknown routes */}
            <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
    );
};

const App = () => {
    return (
        <AuthProvider>
            <Router>
                <AppRoutes />
            </Router>
        </AuthProvider>
    );
};

export default App;
