import React from 'react';
import { useNavigate } from 'react-router-dom';
import goBackIcon from '../icons/goback.webp';
import "./AccountInfo.css"

const AccountInfo = () => {
    const navigate = useNavigate(); // Initialize useNavigate for redirection

    return (
        <div className="Konto-Information">
            <button className="go-back-button" onClick={() => navigate(-1)}>
                <img src={goBackIcon} alt="Go Back" className="go-back-button-img" />
            </button>
            <h1 className="Konto-Information-Title">Konto Information</h1>
            <p className="Konto-Information-Text">View and edit your account information here.</p>
        </div>
    );
};

export default AccountInfo;
