import React from 'react';
import { useNavigate } from 'react-router-dom';
import goBackIcon from '../icons/goback.webp';
import "./TransferMoney.css"

const TransferMoney = () => {
    const navigate = useNavigate();

    return (
        <div className="TransferMoney">
            <button className="go-back-button" onClick={() => navigate(-1)}>
                <img src={goBackIcon} alt="Go Back" className="go-back-button-img" />
            </button>
            <h2>Overfr penge</h2>
            <form>
                <div>
                    <label htmlFor="amount">Belb:</label>
                    <input type="number" id="amount" placeholder="Indtast belb" required />
                </div>
                <div>
                    <label htmlFor="toAccount">Til konto:</label>
                    <input type="text" id="toAccount" placeholder="Indtast konto" required />
                </div>
                <button type="submit">Overfr</button>
            </form>
        </div>
    );
};

export default TransferMoney;
