import React from 'react';
import { useNavigate } from 'react-router-dom';
import goBackIcon from '../icons/goback.webp';
import "./ViewStatements.css"

const ViewStatements = () => {
    const navigate = useNavigate(); // Initialize useNavigate for redirection

    return (
        <div className="ViewStatements">
            <button className="go-back-button" onClick={() => navigate(-1)}>
                <img src={goBackIcon} alt="Go Back" className="go-back-button-img" />
            </button>
            <h2>Se kontoudtog</h2>
            <p>Her kan du se dine kontoudtog.</p>
            {/* Here you might want to implement fetching and displaying the statements */}
        </div>
    );
};

export default ViewStatements;
