import React from 'react';
import { useNavigate } from 'react-router-dom';
import goBackIcon from '../icons/goback.webp';
import "./TwoFactorAuth.css"

const TwoFactorAuth = () => {
    const navigate = useNavigate(); // Initialize useNavigate for redirection

    return (
        <div className="TwoFactorAuth">
            <button className="go-back-button" onClick={() => navigate(-1)}>
                <img src={goBackIcon} alt="Go Back" className="go-back-button-img" />
            </button>
            <h1 className="TwoFactorAuth-Title">To-faktor Godkendelse</h1>
            <p className="TwoFactorAuth-Text">View and edit your To-faktor Godkendelse here.</p>
        </div>
    );
};

export default TwoFactorAuth;
