import React from 'react';
import './Actions.css';
import transferIcon from '../icons/transfer.webp';
import payBillIcon from '../icons/payment.webp';
import depositCheckIcon from '../icons/deposit.webp';
import statementIcon from '../icons/report.webp';
import { useNavigate } from 'react-router-dom'; // Import useNavigate

const Actions = () => {
    const navigate = useNavigate(); // Initialize useNavigate for redirection

    return (
        <div className="actions-container">
            <div className="actions-grid">
                {/* Overfr penge */}
                <div className="action-item" onClick={() => navigate('/dashboard/transfer-money')}>
                    <img src={transferIcon} alt="Overfr penge" className="action-icon" />
                    <p className="action-text">Overfr penge</p>
                </div>

                {/* Betal regninger */}
                <div className="action-item" onClick={() => navigate('/dashboard/pay-bill')}>
                    <img src={payBillIcon} alt="Betal regninger" className="action-icon" />
                    <p className="action-text">Betal regninger</p>
                </div>

                {/* Indbetal check */}
                <div className="action-item" onClick={() => navigate('/dashboard/deposit-check')}>
                    <img src={depositCheckIcon} alt="Indbetal check" className="action-icon" />
                    <p className="action-text">Indbetal check</p>
                </div>

                {/* Se kontoudtog */}
                <div className="action-item" onClick={() => navigate('/dashboard/view-statements')}>
                    <img src={statementIcon} alt="Se kontoudtog" className="action-icon" />
                    <p className="action-text">Se kontoudtog</p>
                </div>
            </div>
        </div>
    );
};

export default Actions;
