import React from 'react';
import './SettingItem.css'; // You can add styles specific to SettingItem here
import redirectIcon from "../icons/forward.webp"; // Make sure this path is correct

const SettingItemRedirect = ({ title, description, onRedirect }) => {
    return (
        <div className="setting-item" onClick={onRedirect}>
            <div className="setting-info">
                <h2 className="setting-Title">{title}</h2>
                <p className="setting-text">{description}</p>
            </div>
            <div className="redirect-icon-con" onClick={onRedirect}>
                <img src={redirectIcon} alt="Redirect" className="redirect-icon" />
            </div>
        </div>
    );
};

export default SettingItemRedirect;
