import React from 'react';
import { useNavigate } from 'react-router-dom';
import goBackIcon from '../icons/goback.webp';
import "./PrivacySettings.css"

const PrivacySettings = () => {
    const navigate = useNavigate(); // Initialize useNavigate for redirection

    return (
        <div className="PrivacySettings">
            <button className="go-back-button" onClick={() => navigate(-1)}>
                <img src={goBackIcon} alt="Go Back" className="go-back-button-img" />
            </button>
            <h1 className="PrivacySettings-Title">Indstillinger for beskyttelse af personlige oplysninger</h1>
            <p className="PrivacySettings-Text">View and edit your Indstillinger for beskyttelse af personlige oplysninger here.</p>
        </div>
    );
};

export default PrivacySettings;
