import React, { useEffect, useState } from 'react';
import { useOutletContext, useNavigate } from 'react-router-dom'; // Import useNavigate for redirection
import SettingItemToggle from '../components/SettingItemToggle'; // Adjusted import path
import SettingItemRedirect from '../components/SettingItemRedirect'; // Import the redirect component
import './Settings.css'; // Import the CSS for styling

const Settings = () => {
    const { user } = useOutletContext(); // Get user from the outlet context
    const [notificationSettings, setNotificationSettings] = useState([]); // State for notification settings
    const [appSettings, setAppSettings] = useState([]); // State for app settings
    const [preferences, setPreferences] = useState({}); // Local state to manage toggle states
    const navigate = useNavigate(); // Initialize useNavigate for redirection

    // Fetch settings data from the user passed via context
    useEffect(() => {
        if (user) {
            const userNotificationSettings = user.settings.notificationPreferences; // Notification settings
            const userAppSettings = user.settings.appSettings; // App settings
            const userPersonalInfo = user.personalInfo; // Personal info

            // Prepare notification settings
            const notificationSettingsData = Object.keys(userNotificationSettings).map(key => ({
                // Add a space before each capital letter (except the first one)
                title: key.charAt(0).toUpperCase() + key.slice(1).replace(/([A-Z])/g, ' $1'),
                description: `Administrer ${key.replace(/([A-Z])/g, ' ').toLowerCase()} meddelelser.`, // Manage notifications
                preferenceKey: key,
                isChecked: !!userNotificationSettings[key], // Ensure this is a boolean
            }));

            // Prepare app settings
            const appSettingsData = Object.keys(userAppSettings).map(key => ({
                // Add a space before each capital letter (except the first one)
                title: key.charAt(0).toUpperCase() + key.slice(1).replace(/([A-Z])/g, ' $1'),
                description: `Administrer ${key.replace(/([A-Z])/g, ' ').toLowerCase()} indstillinger.`, // Manage settings
                preferenceKey: key,
                isChecked: !!userAppSettings[key], // Ensure this is a boolean
            }));

            // Set states
            setNotificationSettings(notificationSettingsData);
            setAppSettings(appSettingsData);
            setPreferences({
                ...userNotificationSettings,
                ...userAppSettings,
                faceID: userPersonalInfo.faceID || false // Initialize faceID to false if undefined
            });
        }
    }, [user]); // Dependency on user data

    // Function to handle toggle changes
    const handleToggle = (preferenceKey) => {
        setPreferences((prev) => ({
            ...prev,
            [preferenceKey]: !prev[preferenceKey], // Toggle the selected preference
        }));
    };

    // Function to handle redirects
    const handleRedirect = (path) => {
        navigate(path); // Use navigate to redirect
    };

    return (
        <div className="settings">
            {/* Notification Settings Container */}
            <div className="settings-Container">
                <div className="settings-TitleCon">
                    <h1 className="settings-Title">Meddelelsesindstillinger</h1> {/* Notification Settings */}
                </div>
                <div className="settings-Box">
                    {notificationSettings.map((setting) => (
                        <SettingItemToggle
                            key={setting.preferenceKey}
                            title={setting.title}
                            description={setting.description}
                            isChecked={preferences[setting.preferenceKey] || false} // Ensure boolean
                            onToggle={() => handleToggle(setting.preferenceKey)}
                        />
                    ))}
                </div>
            </div>

            {/* App Settings Container */}
            <div className="settings-Container">
                <div className="settings-TitleCon">
                    <h1 className="settings-Title">App Indstillinger</h1> {/* App Settings */}
                </div>
                <div className="settings-Box">
                    {appSettings.map((setting) => (
                        <SettingItemToggle
                            key={setting.preferenceKey}
                            title={setting.title}
                            description={setting.description}
                            isChecked={preferences[setting.preferenceKey] || false} // Ensure boolean
                            onToggle={() => handleToggle(setting.preferenceKey)}
                        />
                    ))}
                </div>
            </div>

            {/* Personal Settings Container */}
            <div className="settings-Container">
                <div className="settings-TitleCon">
                    <h1 className="settings-Title">Personlige Indstillinger</h1> {/* Personal Settings */}
                </div>
                <div className="settings-Box">
                    {/* Face ID Redirect */}
                    <SettingItemRedirect
                        title="Face ID"
                        description="Aktiver eller deaktiver Face ID-login." // Enable or disable Face ID login
                        onRedirect={() => handleRedirect('/dashboard/settings/face-id')}
                    />
                    {/* Password Setting Redirect */}
                    <SettingItemRedirect
                        title="Adgangskode"
                        description="Administrer dine adgangskodeindstillinger." // Manage your password settings
                        onRedirect={() => handleRedirect('/dashboard/settings/password')}
                    />
                </div>
            </div>

            {/* Account Settings Container */}
            <div className="settings-Container">
                <div className="settings-TitleCon">
                    <h1 className="settings-Title">Kontoindstillinger</h1> {/* Account Settings */}
                </div>
                <div className="settings-Box">
                    {/* Account Info Redirect */}
                    <SettingItemRedirect
                        title="Konto Information"
                        description="Se og rediger dine kontoinformationer." // View and edit your account information
                        onRedirect={() => handleRedirect('/dashboard/settings/account-info')}
                    />
                    {/* Privacy Settings Redirect */}
                    <SettingItemRedirect
                        title="Privatlivs Indstillinger"
                        description="Administrer dine privatlivsindstillinger." // Manage your privacy preferences
                        onRedirect={() => handleRedirect('/dashboard/settings/privacy-settings')}
                    />
                    {/* Two-Factor Authentication Redirect */}
                    <SettingItemRedirect
                        title="To-faktor Godkendelse"
                        description="Aktiver eller deaktiver to-faktor godkendelse." // Enable or disable two-factor authentication
                        onRedirect={() => handleRedirect('/dashboard/settings/two-factor-auth')}
                    />
                </div>
            </div>
        </div>
    );
};

export default Settings;
