import React from 'react';
import { useNavigate } from 'react-router-dom';
import goBackIcon from '../icons/goback.webp';
import "./FaceIDSettings.css"

const FaceIDSettings = () => {
    const navigate = useNavigate(); // Initialize useNavigate for redirection

    return (
        <div className="Face-ID">
            <button className="go-back-button" onClick={() => navigate(-1)}>
                <img src={goBackIcon} alt="Go Back" className="go-back-button-img" />
            </button>
            <h1 className="Face-ID-Title">Face ID</h1>
            <p className="Face-ID-Text">View and edit your Face-ID here.</p>
        </div>
    );
};

export default FaceIDSettings;
