import React from 'react';
import { useNavigate } from 'react-router-dom';
import goBackIcon from '../icons/goback.webp';
import "./PasswordSettings.css"

const PasswordSettings = () => {
    const navigate = useNavigate(); // Initialize useNavigate for redirection

    return (
        <div className="PasswordSettings">
            <button className="go-back-button" onClick={() => navigate(-1)}>
                <img src={goBackIcon} alt="Go Back" className="go-back-button-img" />
            </button>
            <h1 className="PasswordSettings-Title">Adgangskode indstillinger</h1>
            <p className="PasswordSettings-Text">View and edit your Adgangskode indstillinger here.</p>
        </div>
    );
};

export default PasswordSettings;
