import React from 'react';
import { useNavigate } from 'react-router-dom';
import goBackIcon from '../icons/goback.webp';
import "./DepositCheck.css"

const DepositCheck = () => {
    const navigate = useNavigate(); // Initialize useNavigate for redirection

    return (
        <div className="DepositCheck">
            <button className="go-back-button" onClick={() => navigate(-1)}>
                <img src={goBackIcon} alt="Go Back" className="go-back-button-img" />
            </button>
            <h2>Indbetal check</h2>
            <form>
                <div>
                    <label htmlFor="checkAmount">Belb:</label>
                    <input type="number" id="checkAmount" placeholder="Indtast belb" required />
                </div>
                <div>
                    <label htmlFor="checkNumber">Checknummer:</label>
                    <input type="text" id="checkNumber" placeholder="Indtast checknummer" required />
                </div>
                <button type="submit">Indbetal</button>
            </form>
        </div>
    );
};

export default DepositCheck;
