// src/components/Header.js
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom'; // To navigate to the Notifications page
import './Header.css';
import logOutIcon from '../icons/logout.webp';
import LogoIcon from "../icons/logo.webp"
import notificationIcon from '../icons/notification.webp';
import { useAuth } from '../AuthContext';
import LogoutConfirmation from './LogoutConfirmation'; // Import the confirmation component

const Header = ({ user }) => {
    const navigate = useNavigate();
    const { logout } = useAuth();
    const [hasNewNotifications, setHasNewNotifications] = useState(true); // Assume there are notifications initially
    const [showLogoutConfirmation, setShowLogoutConfirmation] = useState(false); // State to control confirmation visibility

    // Check if user exists
    if (!user) {
        return <div>Indlser brugerdata...</div>;
    }

    // Extract user information
    const { notifications } = user;

    // Calculate the number of notifications
    const notificationCount = notifications ? Object.keys(notifications).length : 0;

    // Logout function
    const handleLogout = () => {
        logout();
        navigate('/'); // Navigate to the home page after logout
    };

    // Handle Notification Click
    const handleNotificationClick = () => {
        setHasNewNotifications(false); // Mark notifications as read
        navigate('/dashboard/notifications'); // Navigate to the Notifications page
    };

    // Handle Logout Icon Click
    const handleLogoutIconClick = () => {
        setShowLogoutConfirmation(true); // Show confirmation modal
    };

    // Handle Logout Confirmation
    const handleConfirmLogout = () => {
        handleLogout(); // Call the logout function
        setShowLogoutConfirmation(false); // Hide confirmation modal
    };

    // Handle Cancel Logout
    const handleCancelLogout = () => {
        setShowLogoutConfirmation(false); // Hide confirmation modal
    };

    return (
        <div className="Header">
            <div className='Logout-con' onClick={handleLogoutIconClick} style={{ cursor: 'pointer' }}>
                <img src={logOutIcon} alt='Logout Icon' className='Logout-icon' />
            </div>
            <div className='Header-con'>
                <img src={LogoIcon} alt='logo' className='Header-con-logo' />
            </div>
            <div className='notification-con'>
                <div className='notification-btn' onClick={handleNotificationClick} style={{ cursor: 'pointer' }}>
                    <img src={notificationIcon} alt='Notification Icon' className='notification-icon' />
                    {/* Only show the notification number if there are new notifications */}
                    {hasNewNotifications && notificationCount > 0 && (
                        <div className="notification-number">
                            <p className='TheNumber'>{notificationCount}</p>
                        </div>
                    )}
                </div>
            </div>

            {/* Render the Logout Confirmation Modal */}
            {showLogoutConfirmation && (
                <LogoutConfirmation
                    onConfirm={handleConfirmLogout}
                    onCancel={handleCancelLogout}
                />
            )}
        </div>
    );
};

export default Header;
