import React, { useEffect, useState } from 'react';
import { Outlet } from 'react-router-dom'; // Import Outlet for rendering nested routes
import Navbar from '../components/Navbar'; // Import the Navbar
import bankingData from '../UserData.json'; // Import your JSON data
import './DashboardLayout.css'; // Import your styles
import Header from '../components/Header'; // Import Header component
import Loader from '../components/Loader'; // Import Loader component
import ScrollToTop from '../components/ScrollToTop'; // Import ScrollToTop component

const DashboardLayout = () => {
    const [user, setUser] = useState(null); // State to store user data
    const [isLoading, setIsLoading] = useState(true); // Loading state

    useEffect(() => {
        const fetchedUser = bankingData.users[0]; // Get the first user from the mock data
        setUser(fetchedUser); // Set the user data
        setIsLoading(false); // Finish loading
    }, []);

    if (isLoading) {
        return <Loader />; // Display loader while fetching data
    }

    return (
        <div className="dashboard-layout">
            <header>
                <Header user={user} />
            </header>
            <main>
                <ScrollToTop />
                {/* Pass user data to child components */}
                <Outlet context={{ user }} />
            </main>
            <footer>
                <Navbar />
            </footer>
        </div>
    );
};

export default DashboardLayout;
