// src/components/LogoutConfirmation.js
import React from 'react';
import './LogoutConfirmation.css'; // Add your own styles
import Logo from "../icons/logo.webp"

const LogoutConfirmation = ({ onConfirm, onCancel }) => {
    return (
        <div className="logout-confirmation-bg">
            <div className="logout-confirmation">
                <img src={Logo} alt='logo' className='logout-confirmation-logo' />
                <h2 className="logout-confirmation-title">Log ud?</h2>
                <p className="logout-confirmation-text">Er du sikker p, at du vil logge ud?</p>
                <div className="confirmation-buttons">
                    <button onClick={onConfirm} className="confirm-button">Ja</button>
                    <button onClick={onCancel} className="cancel-button">Nej</button>
                </div>
            </div>
        </div>
    );
};

export default LogoutConfirmation;
