import React from 'react';
import { useNavigate } from 'react-router-dom'; // Import useNavigate for navigation
import './questions.css';
import goBackIcon from '../icons/goback.webp'; // Import goBack icon
import logoIcon from '../icons/logo.webp'; // Import logo icon

const Questions = () => {
    const navigate = useNavigate();

    const handleGoBack = () => {
        navigate(-1);
    };

    return (
        <div className="Questions_container">
            <div className="Questions">
                {/* Go Back and Logo Icons */}
                <img src={goBackIcon} alt="G tilbage" className="goBack-icon" onClick={handleGoBack} />
                <img src={logoIcon} alt="logo" className="logo-icon" />

                {/* Page Title */}
                <h1>Ofte Stillede Sprgsml (FAQ)</h1>

                {/* FAQ Section */}
                <div className="faq-section">
                    <div className="faq-item">
                        <h2>1. Hvordan nulstiller jeg min PIN?</h2>
                        <p>
                            Hvis du har glemt din PIN, skal du klikke p linket "Glemt PIN" p login-siden og flge instruktionerne.
                            Du kan blive bedt om at bekrfte din identitet.
                        </p>
                    </div>
                    <div className="faq-item">
                        <h2>2. Hvad skal jeg gre, hvis jeg bliver lst ude af min konto?</h2>
                        <p>
                            Efter flere forkerte forsg kan din konto blive lst. Vent venligst i 30 minutter og prv igen.
                            Hvis problemet fortstter, skal du kontakte vores supportteam.
                        </p>
                    </div>
                    <div className="faq-item">
                        <h2>3. Hvor sikker er Face ID-funktionen?</h2>
                        <p>
                            Face ID bruger avanceret biometrisk teknologi for at sikre, at din konto forbliver sikker.
                            Vi anbefaler, at du aktiverer to-faktor-godkendelse for ekstra sikkerhed.
                        </p>
                    </div>
                    <div className="faq-item">
                        <h2>4. Hvordan kan jeg opdatere mine kontooplysninger?</h2>
                        <p>
                            Du kan opdatere dine personlige oplysninger ved at logge ind p din konto, navigere til profilen og redigere dine oplysninger.
                        </p>
                    </div>
                    <div className="faq-item">
                        <h2>5. Hvad skal jeg gre, hvis jeg mistnker svindel?</h2>
                        <p>
                            Hvis du bemrker mistnkelig aktivitet, skal du straks kontakte vores svindelafdeling via vores 24/7 supportlinje eller gennem supportchatten.
                        </p>
                    </div>
                    {/* Add more FAQ items as needed */}
                </div>

                {/* Link back to the Login page */}
                <div className="faq-login-link">
                    <button onClick={handleGoBack} className="back-to-login-button">
                        Tilbage til login
                    </button>
                </div>
            </div>
        </div>
    );
};

export default Questions;
