import React, { useState } from 'react';
import { useParams, useOutletContext, useNavigate } from 'react-router-dom'; // Import useParams to get the account number from the URL
import './AccountDetails.css'; // Style it according to your preference
import TransactionList from '../components/TransactionList'; // Import the TransactionList component
import CreditCards from '../components/CreditCards'; // Import the CreditCards component
import goBackIcon from '../icons/goback.webp';

const AccountDetails = () => {
    const navigate = useNavigate(); // Initialize useNavigate for redirection
    const { accountNumber } = useParams(); // Get the account number from the URL
    const { user } = useOutletContext(); // Get user data passed through Outlet context

    // Find the specific account
    const account = user?.accounts?.find((acc) => acc.accountNumber === accountNumber);

    // Initialize visible transactions state
    const [visibleTransactions, setVisibleTransactions] = useState(10); // Initial visible transactions

    // If the account is not found, show an error message
    if (!account) {
        return <p>Konton findes ikke</p>;
    }

    // Filter for credit cards related to the account
    const relatedCreditCards = user.creditCards.filter(card => card.accountNumber === account.accountNumber);

    // Function to show more transactions
    const handleShowMore = () => {
        setVisibleTransactions((prev) => prev + 10); // Increase the number of visible transactions by 10
    };

    return (
        <div className="account-details">
            <button className="go-back-button" onClick={() => navigate(-1)}>
                <img src={goBackIcon} alt="Go Back" className="go-back-button-img" />
            </button>
            <div className="details">
                <h2 className="account-Title">Kontodetaljer for {account.accountType}</h2>

                <div className="details-info">
                    <p className="account-Balance"><strong>Balance:</strong> {account.balance.toLocaleString('da-DK', { style: 'currency', currency: 'DKK' })}</p>
                    <p className="account-Kontonummer"><strong>Kontonummer:</strong> {account.accountNumber}</p>
                    <CreditCards creditCards={relatedCreditCards} />
                </div>
            </div>
            <TransactionList transactions={account.transactions.slice(0, visibleTransactions)} />
            {visibleTransactions < account.transactions.length && ( // Conditional rendering to show button only if there are more transactions
                <button className="Vis-flere" onClick={handleShowMore}>Vis flere transaktioner</button>
            )}
        </div>
    );
};

export default AccountDetails;
