import React from 'react';
import { useOutletContext } from 'react-router-dom'; // Import useOutletContext to get the data
import './Notifications.css'; // Import styles

const Notifications = () => {
    // Retrieve user from the Outlet context
    const { user } = useOutletContext();
    const notifications = user?.notifications; // Safely access the notifications object

    // If there are no notifications, display a message
    if (!notifications || Object.keys(notifications).length === 0) {
        return <p>No notifications available.</p>;
    }

    return (
        <div className="Notifications">
            <div className="Notifications-data">
                <h2 className="Notifications-title">Dine meddelelser</h2>
                <ul>
                    {Object.entries(notifications).map(([key, value]) => (
                        <li key={key}> <strong>{key}</strong>: <br /> {value}</li>
                    ))}
                </ul>
            </div>
        </div>
    );
};

export default Notifications;
